.summary {
  width: 100%;
  // max-width: 920px;
  display: flex;
  margin: -10px;

  @media screen and (max-width: 768px) {
    flex-wrap: wrap;
  }

  @media screen and (max-width: 500px) {
    margin: 0;
  }
}

.item {
  width: calc(100% / 4 - 10px);
  display: flex;
  flex-direction: column;
  margin: 10px;
  padding: 20px 25px;
  background: var(--greyBackground);

  box-shadow: 4px 4px 20px -10px rgba(0, 0, 0, 0.1);
  border-radius: 12px;

  @media screen and (max-width: 768px) {
    width: calc(100% / 2 - 20px);
  }
  @media screen and (max-width: 500px) {
    width: 100%;
    margin: 10px 0;
  }
}

.title {
  margin-bottom: 20px;

  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  letter-spacing: -0.02em;

  color: var(--white);
}

.amount {
  margin-bottom: 18px;

  font-weight: 700;
  font-size: 28px;
  line-height: 24px;
  color: var(--white);
}

.percent {
  margin-bottom: 8px;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;

  color: rgba(255, 255, 255, 0.6);
}

.percentValue {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  color: var(--lightGreyFontColor);
}
