.CompactCard {
  display: flex;
  flex: 1;
  align-items: center;
  height: 70px !important;
  color: white;
  margin-bottom: 20px;
  padding: 10px 18px;
  position: relative;
  cursor: pointer;

  background: #0d0d0d;
  border: 0.5px solid rgba(109, 125, 147, 0.15);
  box-shadow: 4px 4px 20px -10px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
}
.CompactCard:hover {
  box-shadow: none !important;
}

/* radial bar */
.radialBar {
  //   flex: 1 1;
  width: 48px;
  height: 48px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  //   gap: 1rem;
}
.CircularProgressbar {
  width: 4rem !important;
  overflow: visible;
}
.CircularProgressbar-path {
  stroke: white !important;
  stroke-width: 12px !important;
  filter: drop-shadow(2px 4px 6px white);
}
.CircularProgressbar-trail {
  display: none;
}
.CircularProgressbar-text {
  fill: white !important;
}

.radialBar > span {
  font-size: 17px;
  font-weight: bold;
}

/* sideDetail*/
.detail {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 16px;
}

.title {
  margin-bottom: 7px;
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  text-transform: capitalize;

  color: #ffffff;
}

.term {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 10px;
  line-height: 13px;
  /* identical to box height */

  color: #6d7d93;
}

.shevron {
  width: 24px;
  height: 24px;
}

/* ExpandedCard */

.ExpandedCard {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 16;
  backdrop-filter: blur(4px);

  @media screen and (max-width: 768px) {
    backdrop-filter: none;
  }
}
.ExpandedCardContent {
  background-color: #6d7d93;
  position: relative;
  width: 60%;
  //   height: 70vh;
  max-height: 100vh;
  overflow-y: auto;
  margin: 20px;
  z-index: 12;
  left: 13rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) !important;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 1rem;
  box-shadow: 0px 10px 20px 0px #6d7d93;

  @media screen and (max-width: 1200px) {
    // top: 2rem;
    height: 45vh;
    // left: 6rem;
  }

  @media screen and (max-width: 768px) {
    width: 100%;
    height: 100%;
    z-index: 96;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transform: none !important;
    margin: 0;
    border-radius: 0;
  }
}

.chartContainer {
  width: 70%;
}

.closeBtn {
  cursor: pointer;
  padding: 10px;
  background-color: inherit;
  border: none;

  svg {
    color: #0d0d0d;
  }
}
